@import '~antd/dist/antd.css';

body {
  margin: 0;
  font-family: Manrope3, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  margin: 0;
  font-family: Manrope3, sans-serif;
  font-weight: 900;
  color: white;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1,
.title {
  letter-spacing: 10px;
}

body,
.subtitle {
  letter-spacing: 5px;
}

@font-face {
  font-family: 'Manrope3';
  src: local('Manrope3'), url('./fonts/manrope-regular.ttf') format('truetype');
}

@font-face {
  font-family: 'Manrope3';
  font-weight: 900;
  src: local('Manrope3'), url('./fonts/manrope-extrabold.ttf') format('truetype');
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

