
.App {
  text-align: center;
  will-change: width, height, left, top;
  position: relative;
}

.App-header {
  background-color: #161616;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  font-size: calc(4px + 2vmin);
  padding-left: 5em;
  color: white;
}

.container-title {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background-color: #F5365C;
}

.container-subtitle {
  font-size: calc(10px + 1vmin);
}
